import { Component, OnInit, OnDestroy } from '@angular/core';
import { AuthService } from "angular2-social-login";
import { Injectable } from '@angular/core';
import { Router } from "@angular/router";
import 'rxjs/add/operator/toPromise';
import { ReactiveFormsModule, FormControl, FormGroup, Validators, FormBuilder } from '@angular/forms';
import { UsersSignUp } from "./../services/users_signup";
import { SignupService } from './../services/signup.service';
import { PasswordValidation } from './password-validation';
import { AbstractControl } from '@angular/forms';






@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})



@Injectable()
export class SignupComponent implements OnInit {
  signupForm: FormGroup;

  constructor(private user: SignupService, public _auth: AuthService, public fb: FormBuilder) {

  }

  ngOnInit() {

    this.signupForm = this.fb.group({
      firstname: new FormControl('', [Validators.required, Validators.minLength(3), Validators.maxLength(10), Validators.pattern('[a-zA-Z]+')]),
      lastname: new FormControl('', [Validators.required, Validators.minLength(3), Validators.maxLength(10), Validators.pattern('[a-zA-Z]+')]),
      email: new FormControl('', [Validators.required, Validators.email, Validators.minLength(10), Validators.maxLength(30)]),
      re_email: new FormControl('', [Validators.required, Validators.email, Validators.minLength(10), Validators.maxLength(30)]),
      password: new FormControl('', [Validators.required, Validators.minLength(8), Validators.maxLength(15), Validators.pattern('^[a-zA-Z0-9]*$')]),
      re_password: new FormControl('', [Validators.required, Validators.minLength(8), Validators.maxLength(15), Validators.pattern('^[a-zA-Z0-9]*$')]),
      city: new FormControl('', [Validators.minLength(3), Validators.maxLength(20), Validators.pattern('[a-zA-Z]+')]),
      contact: new FormControl('', [Validators.maxLength(10), Validators.minLength(10)]),
    }, {
        //validator: PasswordValidation.MatchPassword// your validation method
        validator: PasswordValidation.MatchReconfirmation // your validation method
      }
    );
  }

  public onSubmit(data) {

    alert("Registered Successfully.....")
    //console.log("Inside ADDD-----------------", data);
    this.user.save(data);
  }
  public User;
  sub: any;

  signIn(provider) {
    this.sub = this._auth.login(provider).subscribe(
      (data) => {
        console.log(data); this.User = data;
      }
    )
  }

  logout() {
    this._auth.logout().subscribe(
      (data) => { console.log(data); this.User = null; }
    )
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}




