import { Component } from '@angular/core';
import { AbstractControl } from '@angular/forms';





export class PasswordValidation {
    static MatchReconfirmation(AC: AbstractControl) {
        let email = AC.get('email').value;
        //console.log("email>>>>>>>>>>>>>", email);
        let re_email = AC.get('re_email').value;
        //console.log("re_email>>>>>>>>>>>", re_email);
        let password = AC.get('password').value; // to get value in input tag
        //console.log("password>>>>>>>>>>>>", password);
        let re_password = AC.get('re_password').value; // to get value in input tag
        //console.log("re-password>>>>>>>>>>>>>>", re_password);

        if (email != re_email) {
            AC.get('re_email').setErrors({ MatchEmail: true })
        }
        if (password != re_password) {
            AC.get('re_password').setErrors({ MatchPassword: true })
        }
         else {
            return null
        }
    }    
}


